body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  color: black;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin-right: auto;
  margin-left: auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1 {
  font-size: 4rem;
  font-weight: 900;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

h1, h2, h3 {
  margin: 0;
}

span {
  font-size: 3rem;
}

.header {
  background-color: #4CBBB3;
}
.header .header__contenido {
  padding: 2.5rem 0;
}
@media (min-width: 768px) {
  .header .header__contenido {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header h1 {
  color: white;
  margin-bottom: 1rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin-bottom: 0;
  }
}
.header.fijo {
  position: fixed;
  width: 100%;
  box-shadow: 0 0.3rem 0.4rem #4444;
}

.navegacionPrincipal {
  display: flex;
  gap: 2rem;
  justify-content: center;
}
.navegacionPrincipal a {
  color: white;
  font-size: 2rem;
}
.navegacionPrincipal a:hover {
  color: #FEF460;
}

.video {
  position: relative;
  max-height: 70rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(161deg, #020024 0%, rgba(189, 130, 188, 0.55) 99%);
}
.video video {
  display: block;
  width: 100%;
  object-fit: none;
}
.video .contenido-video {
  display: flex;
  align-items: center;
  height: 100%;
}
.video .contenido-video h2 {
  color: white;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-size: 7rem;
}
.video .contenido-video span {
  padding: 1rem 2rem;
  background-color: #F53756;
}

.sobre-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0 5rem;
  }
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-festival h2 {
  font-size: 3rem;
}
.contenido-festival .fecha {
  color: #F53756;
  font-weight: 700;
}
.contenido-festival .texto {
  line-height: 1.5;
}

.lineup {
  background-color: #F53756;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: white;
}
.lineup .dia {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0 5rem;
  }
}
.escenarios-contenedor .escenario {
  padding-top: 2.5rem;
}
.escenarios-contenedor .nombre-escenario {
  color: black;
  font-size: 2rem;
  font-weight: 300;
}
.escenarios-contenedor .nombre-escenario:after {
  content: " ";
  display: block;
  width: 20rem;
  height: 10rem;
  background-repeat: no-repeat;
  background-size: 15rem;
  background-position: 50% 50%;
  margin: 2rem auto 0 auto;
}
.escenarios-contenedor .rock .nombre-escenario::after {
  background-image: url("../img/icono_rock.webp");
}
.escenarios-contenedor .edm .nombre-escenario::after {
  background-image: url("../img/icono_edm.webp");
}
.escenarios-contenedor .calendario {
  padding: 0;
  list-style: none;
}
.escenarios-contenedor .calendario li {
  padding: 2.5rem;
  font-weight: 700;
}
.escenarios-contenedor span {
  font-weight: initial;
  font-size: initial;
}

.bg-verde .calendario li:nth-child(odd) {
  background-color: #3a9a93;
}

.bg-amarillo .calendario li:nth-child(odd) {
  background-color: #fef02d;
}

.galeria .galeria__titulo {
  text-align: center;
  margin: 5rem 0;
}
.galeria .galeria__imagenes {
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem 2rem;
  list-style: none;
}
@media (min-width: 768px) {
  .galeria .galeria__imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem 2rem;
  }
}
@media (min-width: 1200px) {
  .galeria .galeria__imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem 2rem;
  }
}
.galeria img {
  display: block;
  margin-right: auto;
  margin-left: auto;
  cursor: pointer;
}

.mostrarImagen {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.body-fijo {
  overflow: hidden;
}

::-webkit-scrollbar {
  display: none;
}

.boletos {
  padding: 5rem 0;
}
.boletos h3 {
  text-align: center;
  color: white;
}
.boletos .contenedor-boletos {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2.5rem 5rem;
}
.boletos .plan p {
  color: white;
  font-size: 3rem;
  text-align: center;
  margin-bottom: 2rem;
}

.pase {
  width: 100%;
}
.pase thead {
  background-color: #F53756;
}
.pase thead th {
  padding: 1.25rem;
  color: white;
  text-transform: uppercase;
  font-weight: 300;
  font-size: 2rem;
}
.pase tbody {
  background-color: white;
}
.pase tbody td {
  padding: 1.25rem;
  border-bottom: 1px solid #e1e1e1;
}
.pase tbody tr:last-child td {
  border: none;
}
.pase tfoot {
  background-color: #752F97;
}
.pase tfoot td {
  padding: 1.25rem;
  text-align: center;
  font-size: 3rem;
  color: white;
}

.footer {
  background-color: #F53756;
  padding: 1.25rem;
  text-align: center;
}
.footer p {
  color: white;
  margin: 0;
}
/*# sourceMappingURL=app.css.map */
